<template>
  <b-container fluid>
    <PageHeader :header="header" :lead="lead" />
    <b-container v-if="!isLoading" class="p-2 rounded bg-secondary text-white">
      <b-alert v-if="!isOrgContact" variant="info" show>Sign in to view this page.</b-alert>
      <b-row>
        <!--        <b-col>-->
        <!--          <b-form-group-->
        <!--            label="Selection Mode:"-->
        <!--            label-for="table-select-mode-select"-->
        <!--            label-cols-md="4"-->
        <!--          >-->
        <!--            <b-form-select-->
        <!--              id="table-select-mode-select"-->
        <!--              v-model="selectMode"-->
        <!--              :options="modes"-->
        <!--              class="mb-3"-->
        <!--            ></b-form-select>-->
        <!--          </b-form-group>-->
        <!--        </b-col>-->
        <b-col>
          <p><span class="font-weight-bold">Total Recipients:</span> {{total}}</p>
        </b-col>
        <b-col class="mb-2" align="right">
          <span v-if="selected.length > 0"> {{ selected.length }} Item(s) Selected</span>
          <!--          <b-button class="m-1" size="sm" @click="selectAllRows">Select All</b-button>-->
          <!--          <b-button class="m-1" size="sm" @click="clearSelected">Clear Selected</b-button>-->
          <b-button
            class="m-1 border-light font-weight-bold"
            size="sm"
            variant="info"
            @click="applyFilter()">Apply Filters</b-button>
          <b-button
            class="m-1 border-light font-weight-bold"
            size="sm"
            variant="dark"
            @click="clearFilters"
            :disabled="!hasFilters">Clear Filters</b-button>
          <b-button
            class="m-1 border-light font-weight-bold"
            size="sm"
            variant="success"
            @click="reroute('/recipients/create')">Add Recipient</b-button>
        </b-col>
      </b-row>
      <b-row>
        <b-col v-if="isSuperAdmin">
          <b-form-group
            label="Status"
            label-for="table-select-filter-declared"
          >
            <b-form-select
              id="table-select-filter-declared"
              v-model="selectStatus"
              :options="statuses"
              class="mb-3"
            ></b-form-select>
          </b-form-group>
        </b-col>
        <b-col>
          <b-form-group
            label="Historical"
            label-for="table-select-filter-historical"
          >
            <b-form-select
              id="table-select-filter-historical"
              v-model="selectHistorical"
              :options="historical"
              class="mb-3"
            ></b-form-select>
          </b-form-group>
        </b-col>
        <b-col>
          <b-form-group
            label="Sort By"
            label-for="table-select-filter-sort-by"
          >
            <b-form-select
              id="table-select-filter-sort-by"
              v-model="selectSortBy"
              :options="sortBy"
              class="mb-3"
            ></b-form-select>
          </b-form-group>
        </b-col>
        <b-col>
          <b-form-group
            label="Sort Order"
            label-for="table-select-filter-sort-order"
          >
            <b-form-select
              id="table-select-filter-sort-order"
              v-model="selectSortOrder"
              :options="sortOrder"
              class="mb-3"
            ></b-form-select>
          </b-form-group>
        </b-col>
      </b-row>
      <b-row>
        <b-col>
          <b-form-group
            label="Recipient Name"
            label-for="table-input-filter-name"
          >
            <b-form-input
              id="table-input-filter-name"
              v-model="selectName"
              placeholder="Search Recipient's First or Last Name"
            />
            <p class="small">Enter the first <span class="font-weight-bold">OR</span> last name.</p>
          </b-form-group>
        </b-col>
        <b-col>
          <b-form-group label="Milestone" label-for="table-select-filter-milestone">
            <b-form-select
              id="table-select-filter-milestone"
              v-model="selectMilestone"
              :options="milestones"
              class="mb-3"
            ></b-form-select>
          </b-form-group>
        </b-col>
        <b-col>
          <b-form-group
            label="Organization"
            label-for="table-select-filter-organization"
          >
            <b-form-select
              id="table-select-filter-organization"
              v-model="selectOrganization"
              :options="organizations"
              class="mb-3"
            ></b-form-select>
          </b-form-group>
        </b-col>
        <b-col>
          <b-form-group
            label="Retirement"
            label-for="table-select-filter-retiring"
          >
            <b-form-select
              id="table-select-filter-retiring"
              v-model="selectIsRetiring"
              :options="isRetiring"
              class="mb-3"
            ></b-form-select>
          </b-form-group>
        </b-col>
      </b-row>
    </b-container>

    <b-container v-if="!isLoading">
      <b-row>
        <b-col align-self="center" class="overflow-auto text-center">
          <div>
            <b-button
              class="m-2"
              variant="outline-secondary"
              @click="applyFilter(1)">
              <BIconChevronDoubleLeft/>
            </b-button>
            <b-button
              v-for="(link, index) in links"
              v-bind:key="index"
              class="m-2"
              :variant="link.active ? 'info' : 'outline-info'"
              :disabled="!link.url"
              @click="applyFilter(getPageNum(link.url))">
              <BIconCaretLeft v-if="index === 0" />
              <span v-if="index !== 0 && index !== links.length - 1">
                {{ link.label }}
              </span>
              <BIconCaretRight v-if="index === links.length - 1" />
            </b-button>
            <b-button
              class="m-2"
              variant="outline-secondary"
              @click="applyFilter(links.length - 2)">
              <BIconChevronDoubleRight/>
            </b-button>
          </div>
        </b-col>
      </b-row>
    </b-container>

    <b-table
      id="recipients-table"
      ref="selectableTable"
      primary-key="id"
      :items="items"
      small
      :fields="fields"
      striped
      :select-mode="selectMode"
      responsive="sm"
      selectable
      @row-selected="onRowSelected"
      :busy.sync="loading"
    >

      <!-- scoped slot for select state -->
      <template #cell(selected)="{ rowSelected }">
        <template v-if="rowSelected">
          <div class="text-center" aria-hidden="true"><BIconCheckSquare/></div>
          <span class="sr-only">Selected</span>
        </template>
        <template v-else>
          <span aria-hidden="true">&nbsp;</span>
          <span class="sr-only">Not selected</span>
        </template>
      </template>

      <template #cell(is_declared)="row">
        <b-badge :variant="!!row.item.is_declared ? 'success' : 'warning'">
          {{ !!row.item.is_declared ? 'Submitted' : 'Draft' }}
        </b-badge>
        <b-badge v-if="!!row.item.historical" variant="info">Historical</b-badge>
      </template>

      <template #cell(organization)="row">
        {{ lookupOrganization(row.item.organization) }}
      </template>

      <template #cell(retiring_this_year)="row">
        {{ row.item.retiring_this_year ? 'Yes' : 'No' }}
      </template>

      <template #cell(created)="row">
        {{ row.item.created.toLocaleString() }}
      </template>

      <template #cell(updated)="row">
        {{ row.item.updated.toLocaleString() }}
      </template>

      <template #cell(details)="row">
        <b-button
          size="sm"
          class="m-1"
          @click="reroute(`/recipients/view/${row.item.id}`)"
        >
          View
        </b-button>
        <b-button
          size="sm"
          class="m-1"
          @click="reroute(`/recipients/edit/${row.item.id}`)"
        >
          Edit
        </b-button>
        <b-button
          size="sm"
          class="m-1"
          @click="reroute(`/recipients/delete/${row.item.id}`)"
        >
          Delete
        </b-button>
      </template>

    </b-table>

    <b-alert v-if="error" show variant="danger">
      A server error occurred. Please contact the site administrator for assistance.
    </b-alert>

  </b-container>
</template>

<script>

import optionsServices from '@/services/options.services'
import PageHeader from '@/components/PageHeader'
import {
  BIconCheckSquare,
  BIconCaretRight,
  BIconCaretLeft,
  BIconChevronDoubleLeft,
  BIconChevronDoubleRight } from 'bootstrap-vue'
import {getRecipients} from "@/services/api.services";
import optionServices from "@/services/options.services";

export default {
  name: 'recipients-list',
  props: ['header', 'lead', 'mode'],
  components: {
    PageHeader,
    BIconCheckSquare,
    BIconCaretRight,
    BIconCaretLeft,
    BIconChevronDoubleLeft,
    BIconChevronDoubleRight
  },
  data () {
    return {
      loading: false,
      error: false,
      message: '',
      current_page: 0,
      first_page_url: '',
      last_page_url: '',
      last_page: '',
      links: [],
      next_page_url: '',
      path: '',
      per_page: 0,
      prev_page_url: '',
      from: 0,
      to: 0,
      total: 0,
      modes: ['multi', 'single', 'range'],
      selectMode: 'multi',
      selected: [],
      items: [],
      selectHistorical: '',
      selectStatus: '',
      selectMilestone: '',
      selectIsRetiring: '',
      selectOrganization: '',
      selectName: '',
      selectSortBy: '',
      selectSortOrder: '',
      historical: optionServices.get('isHistorical'),
      statuses: optionServices.get('status'),
      isRetiring: optionServices.get('isRetiring'),
      milestones: optionServices.get('milestones'),
      sortBy: optionServices.get('sort-by'),
      sortOrder: optionServices.get('sort-order'),
      fields: [
        {key: 'is_declared', label: 'Status'},
        {key: 'first_name'},
        {key: 'last_name'},
        {key: 'organization'},
        {key: 'branch_name', label: 'Branch'},
        {key: 'milestone'},
        {key: 'retiring_this_year', label: 'Retiring'},
        {key: 'updated'},
        {key: 'created'},
        {key: 'details', label: ''}
      ]
    }
  },
  computed: {
    isLoading () {
      return this.$store.getters.isLoading
    },
    isOrgContact () {
      return this.$store.getters.isOrgContact
    },
    isAdmin () {
      return this.$store.getters.isAdmin
    },
    isSuperAdmin () {
      return this.$store.getters.isSuperAdmin
    },
    organizations() {
      return this.$store.getters.getOrganizations
    },
    hasFilters() {
      return this.selectName !== ''
        || this.selectHistorical !== ''
        || this.selectStatus !== ''
        || this.selectIsRetiring !== ''
        || this.selectMilestone !== ''
        || this.selectOrganization !== ''
        || this.selectSortBy !== ''
        || this.selectSortOrder !== ''
    }
  },
  methods: {
    onRowSelected(items) {
      this.selected = items
    },
    selectAllRows() {
      this.$refs.selectableTable.selectAllRows()
    },
    clearSelected() {
      this.$refs.selectableTable.clearSelected()
    },
    clearFilters() {
      this.selectHistorical = ''
      this.selectStatus = ''
      this.selectName = ''
      this.selectIsRetiring = ''
      this.selectMilestone = ''
      this.selectOrganization = ''
      this.selectSortBy = ''
      this.selectSortOrder = ''
      this.loadRecipients()
    },
    applyFilter (pageNum=null) {
      const filters = []
      if (this.selectHistorical !== '') filters.push({key: 'historical', value: !!this.selectHistorical})
      if (this.selectStatus !== '') filters.push({key: 'status', value: !!this.selectStatus})
      if (this.selectName !== '') filters.push({key: 'name', value: this.selectName})
      if (this.selectIsRetiring !== '') filters.push({key: 'retirement', value: !!this.selectIsRetiring})
      if (this.selectMilestone !== '') filters.push({key: 'milestone', value: this.selectMilestone})
      if (this.selectOrganization !== '') filters.push({key: 'organization', value: this.selectOrganization})
      if (this.selectSortBy !== '') filters.push({
        key: 'sort',
        value: `${this.selectSortBy}${this.selectSortOrder!==''? '+' + this.selectSortOrder : ''}`
      })
      this.loadRecipients(pageNum, filters)
    },
    async loadRecipients (pageNum, filters=[]) {
      this.error = false
      const [error, recipients] = await getRecipients(pageNum, filters)
      if (error) {
        this.error = true
        this.$store.commit('setMessage', error)
      }
      // get data and pagination links
      const {
        data=[],
        current_page=0,
        first_page_url='',
        last_page_url='',
        last_page = '',
        links = [],
        next_page_url = '',
        path = '',
        per_page = 0,
        prev_page_url = '',
        from = 0,
        to = 0,
        total = 0,
      } = recipients || {}

      this.current_page = current_page
      this.first_page_url = first_page_url
      this.last_page_url = last_page_url
      this.last_page = last_page
      this.links = links
      this.next_page_url = next_page_url
      this.path = path
      this.per_page = per_page
      this.prev_page_url = prev_page_url
      this.from = from
      this.to = to
      this.total = total


      // filter draft for non super-admin users
      this.items = (data || [])
        .filter(recipient => {
          return !!recipient.is_declared || this.isSuperAdmin
        })
        .map(recipient => {
          const {
            id=null,
            first_name=false,
            last_name=null,
            milestones=null,
            historical=false,
            retiring_this_year=0,
            organization_id='',
            branch_name='',
            is_declared=0,
            updated_at=null,
            created_at=null,
          } = recipient || {}
          const updatedTS = new Date(updated_at)
          const createdTS = new Date(created_at)
          return {
            id: id,
            is_declared: is_declared,
            first_name: first_name,
            last_name: last_name,
            organization: organization_id,
            historical: historical,
            milestone: milestones,
            branch_name: branch_name,
            retiring_this_year: retiring_this_year,
            created: createdTS,
            updated: updatedTS,
          }
        })
    },
    async reroute(uri) {
      await this.$router.push(uri)
    },
    getPageNum(url) {
      const urlArray = url.split('page=') || []
      return urlArray[urlArray.length - 1]
    },
    lookup(key, value) {
      return optionsServices.lookup(key, value)
    },
    lookupOrganization(value) {
      const org = this.organizations.filter(org => {
        return String(org.value) === String(value)
      })
      if (org.length > 0) {
        const {text = ''} = org[0] || {}
        // return organization shortname
        return text ? text.substring(0, text.lastIndexOf("-")) : '';
      }
      return value
    }
  },
  async beforeMount () {
    this.$store.commit('setLoading', true)
    await this.loadRecipients()
    this.$store.commit('setLoading', false)
  }
}
</script>
